      border: 1px dotted  #2e6e75;
      padding: 2px;
      border-radius:10px;
    }
    .super-flow__node {
      border: 0px solid red;
      background-color: rgba(255,255,255,0);
      box-shadow:1px 2px 8px rgba(0,0,0,0);
      .startNode{
        .inner{
          width: 50px;
          height: 50px;
          border: 1px solid #d9d9d9;
          background-color: #ffffff;
          border-radius:50%;
