          border: 1px solid #d9d9d9;
          background-color: #ffffff;
          border-radius:50%;
          text-align:center;
          line-height:0px;
        }
        .inner:hover{
          width: 50px;
          height: 50px;
          border: 1px solid #2e6e75;
          background-color: #2e6e75;
          color:#ffffff;
          border-radius:50%;
          text-align:center;
          line-height:0px;
        }
      }
      .flow-node {
        border: 1px solid #d9d9d9;
